.modalBackground {
    background: rgba(0, 0, 0, 0.8);
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    cursor: pointer;
    /* указываем z-индекс для корректного наслаивания */
    z-index: 101;

    &.visible {
        display: block;
    }
}

.modal {
    position: absolute;
    min-width: 500px;
    height: 60vh;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    align-items: center;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    border-radius: 10px;
    background-color: rgb(255, 255, 255);
    cursor: default;

    &__header {
        justify-content: space-between;
        width: 100%;
        height: 65px;
        padding: 10px;
        border-bottom: 1px solid #dfdfdf;
    }

    &__content {
        width: 100%;
        overflow: auto;
        padding: 10px;
    }
}